import { ListGroupPlugin as BambooListGroup } from '@ali/4ever-bamboo';
import { zhiGroupFactory as groupFactory } from '@ali/4ever-factory';

const { createRenderNode, createGroupNodes } = BambooListGroup.handlers;

const ListGroupPlugin = groupFactory((config) => {
  const { listGroup = {} } = config;
  return {
    groupType: 'listGroup',
    renderNode: createRenderNode(listGroup),
    groupNodes: createGroupNodes(listGroup),
    queries: {
      isListGroupAvailable: () => listGroup.ignore ? !listGroup.ignore() : true,
    },
  };
});

export default ListGroupPlugin;
